//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

.btn {
  display: inline-block;
  margin-bottom: 0; // For input.btn
  font-weight: @btn-font-weight;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  white-space: nowrap;
  .button-size(@btn-padding-vertical; @btn-padding-horizontal; @font-size-base; @line-height-base; @border-radius-base);
  .user-select(none);

  &:focus {
    //.tab-focus();
    outline: thin;
  }

  &:hover,
  &:focus {
    color: @btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: not-allowed;
    pointer-events: none; // Future-proof disabling of clicks
    background: none; // Remove the gradient from normal backgrounds
    background-color:  rgba(red(@btn-default-bg), green(@btn-default-bg), blue(@btn-default-bg), @btn-disabled-opacity);
    border-color:  rgba(red(@btn-default-border), green(@btn-default-border), blue(@btn-default-border), @btn-disabled-opacity);
    .box-shadow(none);
  }
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  .button-variant(@btn-default-color; @btn-default-color-disabled; @btn-default-bg-top; @btn-default-bg-bottom; @btn-default-bg-hover-top; @btn-default-bg-hover-bottom; @btn-default-border; @btn-disabled-opacity);
}
.btn-primary {
  .button-variant(@btn-primary-color; @btn-primary-color-disabled; @btn-primary-bg-top; @btn-primary-bg-bottom; @btn-primary-bg-hover-top; @btn-primary-bg-hover-bottom; @btn-primary-border; @btn-primary-disabled-border-opacity);
}
// Success appears as green
.btn-success {
  .button-variant(@btn-success-color; @btn-success-color; @btn-success-bg; @btn-success-bg; @btn-success-bg-hover; @btn-success-bg-hover; @btn-success-border; @btn-disabled-opacity);
}
// Info appears as blue-green
.btn-info {
  .button-variant(@btn-info-color; @btn-info-color; @btn-info-bg; @btn-info-bg; @btn-info-bg-hover; @btn-info-bg-hover; @btn-info-border; @btn-disabled-opacity);
}
// Warning appears as orange
.btn-warning {
  .button-variant(@btn-warning-color; @btn-warning-color; @btn-warning-bg; @btn-warning-bg; @btn-warning-bg-hover; @btn-warning-bg-hover; @btn-warning-border; @btn-disabled-opacity);
}
// Danger and error appear as red
.btn-danger {
  .button-variant(@btn-danger-color; @btn-danger-color; @btn-danger-bg; @btn-danger-bg; @btn-danger-bg-hover; @btn-danger-bg-hover; @btn-danger-border; @btn-disabled-opacity);
}


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: @link-color;
  font-weight: normal;
  cursor: pointer;
  border-radius: 0;

  &,
  &:active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    .box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: underline;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: @btn-link-disabled-color;
      text-decoration: none;
    }
  }
}


// Button Sizes
// --------------------------------------------------

.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  .button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @border-radius-large);
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  .button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @border-radius-small);
}
.btn-xs {
  .button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-small; @line-height-small; @border-radius-small);
}


// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}


// Dropdown button
// --------------------------------------------------

.btn-dropdown {
  position: relative;
  padding: 5px 30px 5px 12px;
  // Roboto Regular
  font-weight: 400;
  font-style: normal;
  width: 100%;
  text-align: left;

  .icon-dropdown-arrow{
    // margin-left: 87px;
    color: @genesys-dark-blue;
    position: absolute;
    font-size: 15px;
    top: 1.5px;
    right: 12px;
  }
  .icon-expand-up{
    // margin-left: 87px;
  }

}

.open {
  .icon-dropdown-arrow:before{
  -ms-transform:rotate(180deg); /* IE 9 */
-webkit-transform:rotate(180deg); /* Chrome, Safari, Opera */
transform:rotate(180deg); /* Standard syntax */
}

